.market-box {
    width: 690px;
    min-height: 350px;
    border: 1px solid #e3e3e3;
    position: absolute;
    top: 42px;
    left: 0;
    z-index: 2;
    background: #fff;
    &:before {
        position: absolute;
        content: '';
        background: url("../img/icon/market-triangle.png") no-repeat;
        left: 78px;
        top: -12px;
        width: 18px;
        height: 12px;
    }

    .list {
        overflow: hidden;

        .td {
            float: left;
            height: 50px;
            line-height: 50px;
            padding: 0 8px;
            position: relative;
            box-sizing: border-box;
            font-size: 12px;

            &:first-child:before {
                display: none;
            }
        }

        &:nth-of-type(even) {
            background: #f0f7fa;
        }

        &.click {
            cursor: pointer;

            &:hover {
                background: #fffcf3;
            }
        }

        &.line-2 .td {
            height: 52px;
            line-height: 52px;

            .text {
                line-height: 18px;
            }
        }

        .light {
            color: #777;
        }
    }

    &.table-25 {
        .td {
            width: 25%;
        }
    }

    @each $item in 15, 20, 25, 30, 40, 100 {
        .td-#{$item} {
            width: $item*1%;
        }
    }

    .thead {
        border-bottom: 1px solid #90cee3;
        text-align: center;
    }

    .tbody {
        max-height: 468px;
        position: relative;
    }

    &.no-border {
        border: 0;

        .list .td:before {
            display: none;
        }
    }
}
.market-hide {
    display: none;
}
