.safety-container, .asset-container{
    width: 1200px;
    margin: auto;
    padding: 20px 0;
    .center-box{
        position: relative;
        border: solid #e3e3e3 1px;
	    margin-top: 14px;

        @include clearfix();
    }
    .center-left{
        position: absolute;
        height: 100%;
        left: 0;
        margin-right: -1px;
        border-right: solid 1px $gray;
        .center-list{
            width: 300px;
            li{
                position: relative;
                padding: 15px 10px 15px 22px;
                margin-top: -1px;
                cursor: pointer;
                border-left: solid 5px transparent;
                border-top: solid 1px transparent;
                border-bottom: solid 1px transparent;
                font-size: 14px;
                color: #666;
            }
            a{
                font-size: 14px;
            }
            .icon{
                @include vertical(sub);
                width: 25px;
                height: 22px;
                margin-right: 10px;
                padding: 0 !important;
            }
            .safety{
                @include seticonbg('../img/common/icon-safety', 'off');
            }
            .shiming{
                @include seticonbg('../img/common/icon-shiming', 'off');
            }
            .idCard{
                @include seticonbg('../img/common/icon-idCard', 'off');
            }
            .code1{
                @include seticonbg('../img/common/icon-code1', 'off');
            }
            .code2{
                @include seticonbg('../img/common/icon-code2', 'off');
            }
            .zcgl{
                @include seticonbg('../img/common/icon-zcgl', 'off');
            }
            .chongzhi{
                @include seticonbg('../img/common/icon-chongzhi', 'off');
            }
            .tixian{
                @include seticonbg('../img/common/icon-tixian', 'off');
            }
            .yhk{
                @include seticonbg('../img/common/icon-yhk', 'off');
            }
            .wt{
                @include seticonbg('../img/common/icon-wt', 'off');
            }
            .cx{
                @include seticonbg('../img/common/icon-cx', 'off');
            }
            .jlr{
                @include seticonbg('../img/common/icon-jlr', 'off');
            }
            .incoin{
                @include seticonbg('../img/common/icon-incoin', 'off');
            }
            .outcoin{
                @include seticonbg('../img/common/icon-outcoin', 'off');
            }
            .manage{
                @include seticonbg('../img/common/icon-management', 'off');
            }
            .recharge{
                @include seticonbg('../img/common/icon-recharge', 'off');
            }
            .deposit{
                @include seticonbg('../img/common/icon-deposit', 'off');
            }
            .bankCard{
                @include seticonbg('../img/common/icon-bankCard', 'off');
            }
            .entrust{
                @include seticonbg('../img/common/icon-entrustManagement', 'off');
                border:none;
            }
            .record{
                @include seticonbg('../img/common/icon-record', 'off');
            }
            .handler{
                @include seticonbg('../img/common/icon-handler', 'off');
            }
            .arrows{
                position: absolute;
                line-height: 23px;
                color: transparent;
                font-size: $fz-small;
                right: 20px;
            }
            //鼠标选中效果
            li:hover, .selected{
                background: $body-bg;
            }
            li:hover, li:hover .arrows, .selected, .selected .arrows{
                color: $main;
            }
            //icon颜色
            li:hover .safety, .selected .safety{
                @include seticonbg('../img/common/icon-safety', 'on');
            }
            li:hover .shiming, .selected .shiming{
                @include seticonbg('../img/common/icon-shiming', 'on');
            }
            li:hover .idCard, .selected .idCard{
                @include seticonbg('../img/common/icon-idCard', 'on');
            }
            li:hover .code1, .selected .code1{
                @include seticonbg('../img/common/icon-code1', 'on');
            }
            li:hover .code2, .selected .code2{
                @include seticonbg('../img/common/icon-code2', 'on');
            }
            li:hover .zcgl, .selected .zcgl{
                @include seticonbg('../img/common/icon-zcgl', 'on');
            }
            li:hover .chongzhi, .selected .chongzhi{
                @include seticonbg('../img/common/icon-chongzhi', 'on');
            }
            li:hover .tixian, .selected .tixian{
                @include seticonbg('../img/common/icon-tixian', 'on');
            }
            li:hover .yhk, .selected .yhk{
                @include seticonbg('../img/common/icon-yhk', 'on');
            }
            li:hover .wt, .selected .wt{
                @include seticonbg('../img/common/icon-wt', 'on');
            }
            li:hover .cx, .selected .cx{
                @include seticonbg('../img/common/icon-cx', 'on');
            }
            li:hover .jlr, .selected .jlr{
                @include seticonbg('../img/common/icon-jlr', 'on');
            }
            li:hover .incoin, .selected .incoin{
                @include seticonbg('../img/common/icon-incoin', 'on');
            }
            li:hover .outcoin, .selected .outcoin{
                @include seticonbg('../img/common/icon-outcoin', 'on');
            }
            li:hover .manage,.selected .manage{
                @include seticonbg('../img/common/icon-management', 'on');
            }
            li:hover .recharge,.selected .recharge{
                @include seticonbg('../img/common/icon-recharge', 'on');
            }
            li:hover .deposit,.selected .deposit{
                @include seticonbg('../img/common/icon-deposit', 'on');
            }
            li:hover .bankCard,.selected .bankCard{
                @include seticonbg('../img/common/icon-bankCard', 'on');
            }
            li:hover .entrust,.selected .entrust{
                @include seticonbg('../img/common/icon-entrustManagement', 'on');
            }
            li:hover .record,.selected  .record{
                @include seticonbg('../img/common/icon-record', 'on');
            }
            li:hover .handler,.selected .handler{
                @include seticonbg('../img/common/icon-handler', 'on');
            }
            //border-left
            li:hover, .selected{
                color: $main-sub;
                width: 302px;
                background: #fff;
                border-left: solid $main 5px;
                border-top: solid $gray 1px;
                border-bottom: solid $gray 1px;
            }
        }
    }
    .center-right {
        position: relative;
	    width: 896px;
	    min-height: 635px !important;
	    background: $white;
	    float: right;
	    padding-bottom: 20px;
    }
}