.assets-deposit {
    .heading {
        height: 54px;
        line-height: 54px;
        font-size: 24px;
    }

    .light {
        color: #777;
    }

    .prompt {
        color: $main;
    }

    .formbox {
        padding: 40px 20px;

        li {
            margin-bottom: 15px;

            @include clearfix;

            margin-top: 15px;
            position: relative;

            label {
                display: block;
                float: left;
                width: 170px;
                text-align: right;
                height: 34px;
                line-height: 34px;
                margin-right: 6px;
            }

            .inp {
                float: left;
                display: block;
                padding: 8px 12px;
                height: 34px;
                width: 300px;
                border: 1px solid #ddd;
                outline: none;
                border-radius: 3px;
                background: none;
                margin-right: 6px;
            }

            .inp-s {
                display: block;
                padding: 0 12px;
                height: 34px;
                line-height: 34px;
            }

            .point {
                color: $main;
            }

            .txBtn {
                display: block;
                width: 230px;
                margin-left: 35px;
                height: 44px;
                line-height: 44px;
                font-size: 16px;
            }
        }

        .msg-tip {
            text-indent: 166px;
            color: #f00;
            position: absolute;
            top: -6px;
            left: 0;
            display: none;
        }
    }

    .tabTit {
        font-size: 16px;
        margin: 20px 0 5px;
    }

    .record-info {
        .record-header {
            background: #f0f7fa;

            @include clearfix();

            span {
                height: 45px;
                line-height: 45px;
                width: 16.6%;
                display: block;
                text-align: center;
                float: left;
                white-space: nowrap;
            }
        }

        .record-centre {
            table {
                width: 100%;
            }

            max-height: 630px;
            overflow-x: hidden;
            overflow-y: auto;
        }

        td {
            width: 16.6%;
            text-align: center;
            color: #333;
            padding: 8px 0;

            &.nonote {
                color: #999;
            }
        }

        .odd {
            background: #fff;
        }

        .even {
            background: #fafafa;
        }
    }
}
