/*
 *  资产管理公共样式
 * */
$borColor: #ddd;

.assets-content {
    padding-left: 30px;
    padding-right: 30px;

    .center-title {
        font-size: 20px;
        margin: 15px auto;
    }

    .table {
        width: 100%;
        border-collapse: collapse;
        text-align: center;
        border: 1px solid #e8e8e8;
        margin-top: 14px;

        thead {
            background: #e5f2f7;
        }

        th,
        td {
            font-weight: 500;
            padding: 8px;
            word-wrap: break-word;
            vertical-align: middle;
        }

        tbody tr:nth-of-type(even) {
            background: #f0f7fa;
        }
    }

    .div-select {
        width: 174px;
        height: 34px;
        line-height: 34px;
        border: 1px solid $borColor;
        background: #fff;
        position: relative;
        cursor: pointer;
        display: inline-block;
        z-index: 1;
        box-sizing: content-box;

        &.select-small {
            width: 140px;
            height: 26px;
            line-height: 26px;
            font-size: 13px;
        }

        cite {
            padding: 0;
            width: 100%;
            text-indent: 10px;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 2;
            font-style: normal;
            color: #666;
            display: block;
            text-align: left;
        }

        cite span {
            color: #999;
        }

        i {
            width: 0;
            height: 0;
            position: absolute;
            right: 10px;
            top: 50%;
            margin-top: -3px;
            border-width: 6px 4px;
            border-style: solid;
            border-color: #666 transparent transparent transparent;
        }

        ul {
            width: 100%;
            border: 1px solid $borColor;
            max-height: 200px;
            background-color: #fff;
            position: absolute;
            left: -1px;
            top: 100%;
            box-sizing: content-box;
            box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, .08);
            overflow: auto;
            display: none;
        }

        ul li {
            height: 32px;
            line-height: 32px;
            border-top: 1px solid #dedede;
            box-sizing: content-box;
        }

        ul li:first-child {
            border-top: none;
        }

        ul li a {
            display: block;
            color: #404040;
            text-decoration: none;
            padding: 0 10px;
            box-sizing: content-box;
        }

        ul li a:hover {
            background-color: #fffcf3;
            color: #d02c2c;
        }
    }

    .select-box {
        border: 1px $borColor solid;

        @include clearfix();

        .div-select {
            float: left;
            border: 0;
            border-right: 1px solid $borColor;
        }

        .selectbox_p,
        .select_p {
            padding: 8px 12px 7px;
        }

        .select_p {
            margin-right: 80px;
            float: left;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    .record-box {
        margin-top: 30px;
    }

    .info_list_title h3 {
        font-weight: 500;
        font-size: 18px;
    }

    .record-info {
        border: 1px solid #e8e8e8;
    }
}
