.assetsManagementLH {
    padding-right: 30px;

    .selectbox {
        margin-bottom: 10px;
        width: 100%;
        overflow: hidden;

        @include clearfix();

        select,
        input[type=text] {
            width: 180px;
            height: 35px;
        }

        input[type=text] {
            padding: 0 5px;
        }

        .inp_time {
            margin-left: 5px;

            input[type=text] {
                width: 220px;
            }
        }
    }
}

.assets-entrust {
    .record-info {
        .record-header {
            span {
                width: 12.5%;
            }
        }

        td {
            width: 12.5%;
        }

        .remove {
            display: block;
        }
    }
}
