.assetsManagementLH {
    em {
        font-style: normal;
    }

    .center-title {
        font-size: 20px;
        margin-bottom: 25px;
    }

    .p2 {
        font-size: 15px;
        height: 40px;
    }

    .inp_text {
        height: 35px;
        width: 600px;
        padding: 0 5px;
        margin: 5px 0;
    }

    .ManageBtn {
        background-color: $main-sub;
        height: 35px;
        line-height: 25px;
        padding: 5px 10px;
        color: #fff;
        border: none;
        cursor: pointer;

        &:hover {
            color: #fff;
        }
    }

    .invit_des {
        padding-top: 10px;
        font-size: 12px;
        color: #999;
    }

    .tabTit {
        font-size: 16px;
        margin: 20px 0 5px;
    }

    .record-info {
        .record-header {
            background: #e5f2f7;

            @include clearfix();

            span {
                padding: 8px;
                width: 25%;
                display: block;
                text-align: center;
                float: left;
                box-sizing: border-box;
            }
        }

        .record-centre {
            table {
                width: 100%;
            }

            max-height: 630px;
            overflow-x: hidden;
            overflow-y: auto;
        }

        td {
            width: 25%;
            text-align: center;
            color: #333;
            padding: 8px 0;

            &.nonote {
                color: #999;
            }
        }

        .odd {
            background: #fff;
        }

        .even {
            background: #fafafa;
        }
    }

    .QR {
        margin: 15px 0;
        min-height: 100px;
        font-size: 0;

        img {
            width: 100px;
            height: 100px;
        }
    }
}
